// Core
import { handleActions } from 'redux-actions';
import { List, fromJS } from 'immutable';

// Actions
import { tasksActions } from 'bus/tasks/actions';
import { uiActions } from 'bus/ui/actions';

const initialState = List();

export const tasksReducer = handleActions(
    {
        [tasksActions.fetchTasksSuccess]: (state, { payload }) => {
            return fromJS(payload);
        },
        [tasksActions.createTaskSuccess]: (state, { payload }) => {
            return state.unshift(fromJS(payload));
        },
        [tasksActions.removeTaskSuccess]: (state, { payload }) => {
            return state.filter((post) => post.get('id') !== payload);
        },
        [tasksActions.updateTaskSuccess]: (state, { payload }) => {
            return state.update(
                state.findIndex((task) => task.get('id') === payload.id),
                () => fromJS(payload)
            );
        },
        [tasksActions.completeAllTasksSuccess]: (state) => {
            return state.map((task) => task.set('completed', true));
        },
        [uiActions.confirmEditingTask]: (state, { payload }) => {
            return state.update(
                state.findIndex((task) => task.get('id') === payload.id),
                (task) => task.merge(payload)
            );
        },
    },
    initialState
);
