// Core
import { call, put, select } from 'redux-saga/effects';

// Instruments
import { url, token } from 'config/api';
import { tasksActions } from 'bus/tasks/actions';
import { uiActions } from 'bus/ui/actions';

export function* callCompleteAllTasksWorker () {
    try {
        yield put(uiActions.setTasksFetchingState(true));

        const tasks = yield select((state) => state.tasks);
        const completedTasks = tasks
            .map((task) => task.set('completed', true))
            .toJS();
        const promises = completedTasks.map((task) =>
            fetch(url, {
                method:  'PUT',
                headers: {
                    Authorization:  token,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify([task]),
            })
        );
        const responses = yield call([Promise, Promise.all], promises);
        const success = responses.every((result) => result.status === 200);

        if (!success) {
            throw new Error('Posts was not completed');
        }

        yield put(tasksActions.completeAllTasksSuccess());
    } catch (error) {
        yield put(tasksActions.completeAllTasksFail(error));
    } finally {
        yield put(uiActions.setTasksFetchingState(false));
    }
}
