// Core
import { takeEvery } from 'redux-saga/effects';

// Instruments
import { tasksActions } from 'bus/tasks/actions';
import {
    callFetchTasksWorker,
    callCreateTaskWorker,
    callRemoveTaskWorker,
    callUpdateTaskWorker,
    callCompleteAllTasksWorker
} from './workers';

export const tasksWatchers = Object.freeze({
    * watchFetchTasks () {
        yield takeEvery(tasksActions.fetchTasks, callFetchTasksWorker);
    },
    * watchCreateTask () {
        yield takeEvery(tasksActions.createTask, callCreateTaskWorker);
    },
    * watchRemoveTask () {
        yield takeEvery(tasksActions.removeTask, callRemoveTaskWorker);
    },
    * watchUpdateTask () {
        yield takeEvery(tasksActions.updateTask, callUpdateTaskWorker);
    },
    * watchCompleteAllTasks () {
        yield takeEvery(
            tasksActions.completeAllTasks,
            callCompleteAllTasksWorker
        );
    },
});
