// Core
import { createActions } from 'redux-actions';

// Types
import types from './types';

export const tasksActions = createActions({
    [types.FETCH_TASKS]:                void 0,
    [types.FETCH_TASKS_SUCCESS]:        (tasks) => tasks,
    [types.FETCH_TASKS_FAIL]:           (error) => error,
    [types.CREATE_TASK]:                (message) => message,
    [types.CREATE_TASK_SUCCESS]:        (task) => task,
    [types.CREATE_TASK_FAIL]:           (error) => error,
    [types.REMOVE_TASK]:                (id) => id,
    [types.REMOVE_TASK_SUCCESS]:        (id) => id,
    [types.REMOVE_TASK_FAIL]:           (error) => error,
    [types.UPDATE_TASK]:                (updatedTask) => updatedTask,
    [types.UPDATE_TASK_SUCCESS]:        (updatedTask) => updatedTask,
    [types.UPDATE_TASK_FAIL]:           (error) => error,
    [types.COMPLETE_ALL_TASKS]:         void 0,
    [types.COMPLETE_ALL_TASKS_SUCCESS]: void 0,
    [types.COMPLETE_ALL_TASKS_FAIL]:    (error) => error,
});
