// Core
import { all } from 'redux-saga/effects';

// Instruments
import { tasksWatchers } from 'bus/tasks/saga/watchers';

export function* rootSaga () {
    yield all([
        tasksWatchers.watchFetchTasks(),
        tasksWatchers.watchCreateTask(),
        tasksWatchers.watchRemoveTask(),
        tasksWatchers.watchUpdateTask(),
        tasksWatchers.watchCompleteAllTasks()
    ]);
}
