// Core
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Form, Control } from 'react-redux-form';
import Move from 'react-flip-move';

// Instruments
import Styles from './styles.m.css';
import { tasksActions } from '../../bus/tasks/actions';
import { selectSortedTasks } from '../../bus/tasks/selectors';
import { uiActions } from '../../bus/ui/actions';

// Components
import Task from '../Task';
import Spinner from '../Spinner';
import Checkbox from '../../theme/assets/Checkbox';

const mapStateToProps = (state) => {
    return {
        tasks:           selectSortedTasks(state),
        isTasksFetching: state.ui.get('isTasksFetching'),
        tasksFilter:     state.ui.get('tasksFilter'),
        editingTask:     state.ui.get('editingTask'),
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        actions: bindActionCreators(
            {
                fetchTasks:               tasksActions.fetchTasks,
                createTask:               tasksActions.createTask,
                removeTask:               tasksActions.removeTask,
                updateTask:               tasksActions.updateTask,
                completeAllTasks:         tasksActions.completeAllTasks,
                updateTasksFilter:        uiActions.updateTasksFilter,
                startEditingTask:         uiActions.startEditingTask,
                confirmEditingTask:       uiActions.confirmEditingTask,
                resetEditingTask:         uiActions.resetEditingTask,
                updateEditingTaskMessage: uiActions.updateEditingTaskMessage,
            },
            dispatch
        ),
    };
};

@connect(
    mapStateToProps,
    mapDispatchToProps
)
export default class Scheduler extends Component {
    componentDidMount () {
        this.props.actions.fetchTasks();
    }

    _createTask = ({ newTaskMessage }) => {
        newTaskMessage && this.props.actions.createTask(newTaskMessage);
    };

    _updateTasksFilter = (event) => {
        this.props.actions.updateTasksFilter(event.target.value.toLowerCase());
    };

    _getAllCompleted = () => this.props.tasks.every((task) => task.get('completed'));

    render () {
        const { actions, tasks, tasksFilter, isTasksFetching, editingTask } = this.props;

        const allCompleted = this._getAllCompleted();
        const todoList = tasks
            .filter((task) =>
                task
                    .get('message')
                    .toLowerCase()
                    .includes(tasksFilter)
            )
            .map((task) => (
                <Task
                    completed = { task.get('completed') }
                    confirmEditingTask = { actions.confirmEditingTask }
                    editingTask = { editingTask }
                    favorite = { task.get('favorite') }
                    id = { task.get('id') }
                    key = { task.get('id') }
                    message = { task.get('message') }
                    removeTask = { actions.removeTask }
                    resetEditingTask = { actions.resetEditingTask }
                    startEditingTask = { actions.startEditingTask }
                    updateEditingTaskMessage = { actions.updateEditingTaskMessage }
                    updateTask = { actions.updateTask }
                    { ...task }
                />
            ));

        return (
            <section className = { Styles.scheduler }>
                <Spinner isSpinning = { isTasksFetching } />
                <main>
                    <header>
                        <h1> </h1>
                        <input
                            placeholder = ''
                            type = 'search'
                            value = { tasksFilter }
                            onChange = { this._updateTasksFilter }
                        />
                    </header>
                    <section>
                        <Form model = 'forms.scheduler' onSubmit = { this._createTask }>
                            <Control.input
                                className = { Styles.createTask }
                                maxLength = { 50 }
                                model = '.newTaskMessage'
                                placeholder = '   '
                                type = 'text'
                            />
                            <button> </button>
                        </Form>
                        <div className = { Styles.overlay }>
                            <ul>
                                <Move duration = { 400 } easing = 'ease-in-out'>
                                    {todoList}
                                </Move>
                            </ul>
                        </div>
                    </section>
                    <footer>
                        <Checkbox
                            checked = { allCompleted }
                            color1 = '#363636'
                            color2 = '#fff'
                            onClick = { !allCompleted && actions.completeAllTasks }
                        />
                        <span className = { Styles.completeAllTasks }>  </span>
                    </footer>
                </main>
            </section>
        );
    }
}
