// Core
import { handleActions } from 'redux-actions';
import { fromJS } from 'immutable';

// Actions
import { uiActions } from './actions';

const initialState = fromJS({
    isTasksFetching: false,
    tasksFilter:     '',
    editingTask:     {
        id:         '',
        newMessage: '',
        mutated:    false,
    },
});

export const uiReducer = handleActions(
    {
        [uiActions.setTasksFetchingState]: (state, { payload }) =>
            state.set('isTasksFetching', payload),
        [uiActions.updateTasksFilter]: (state, { payload }) =>
            state.set('tasksFilter', payload),
        [uiActions.startEditingTask]: (state, { payload }) =>
            state.update('editingTask', (editingTask) =>
                editingTask.merge(
                    fromJS({
                        id:         payload.id,
                        newMessage: payload.initialMessage,
                    })
                )
            ),
        [uiActions.resetEditingTask]: (state) =>
            state.set('editingTask', initialState.get('editingTask')),
        [uiActions.updateEditingTaskMessage]: (state, { payload }) =>
            state.setIn(['editingTask', 'newMessage'], payload),
    },
    initialState
);
