// Core
import { createActions } from 'redux-actions';

// Types
import types from './types';

export const uiActions = createActions({
    [types.SET_TASKS_FETCHING_STATE]: (state) => state,
    [types.UPDATE_TASKS_FILTER]:      (filter) => filter,
    [types.START_EDITING_TASK]:       (id, initialMessage) => ({
        id,
        initialMessage,
    }),
    [types.CONFIRM_EDITING_TASK]:        (id, message) => ({ id, message }),
    [types.RESET_EDITING_TASK]:          void 0,
    [types.UPDATE_EDITING_TASK_MESSAGE]: (message) => message,
});
