// Core
import { call, put } from 'redux-saga/effects';
import { actions } from 'react-redux-form';

// Instruments
import { url, token } from 'config/api';
import { tasksActions } from 'bus/tasks/actions';
import { uiActions } from 'bus/ui/actions';

export function* callCreateTaskWorker ({ payload: message }) {
    try {
        yield put(uiActions.setTasksFetchingState(true));

        const response = yield call(fetch, url, {
            method:  'POST',
            headers: {
                Authorization:  token,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ message }),
        });

        const { data: tasks, message: errorMessage } = yield call([
            response,
            response.json
        ]);

        if (response.status !== 200) {
            throw new Error(errorMessage);
        }

        yield put(tasksActions.createTaskSuccess(tasks));
        yield put(actions.reset('forms.scheduler'));
    } catch (error) {
        yield put(tasksActions.createTaskFail(error));
    } finally {
        yield put(uiActions.setTasksFetchingState(false));
    }
}
