// Core
import React, { PureComponent, createRef } from 'react';
import cx from 'classnames';

// Instruments
import Styles from './styles.m.css';

// Components
import Checkbox from '../../theme/assets/Checkbox';
import Remove from '../../theme/assets/Remove';
import Edit from '../../theme/assets/Edit';
import Star from '../../theme/assets/Star';

export default class Task extends PureComponent {
    componentDidUpdate () {
        this.input.current.focus();
    }

    input = createRef();

    _getTaskShape = ({
        id,
        completed = this.props.completed,
        favorite = this.props.favorite,
        message = this.props.message,
    }) => ({
        id,
        completed,
        favorite,
        message,
    });

    _toggleEditingState = () => {
        const { startEditingTask, resetEditingTask, editingTask, id, message } = this.props;

        editingTask.get('id') === id ? resetEditingTask() : startEditingTask(id, message);
    };

    _editNewMessage = (event) => {
        const { updateEditingTaskMessage } = this.props;
        const nextMessage = event.target.value;

        nextMessage.length < 50 && updateEditingTaskMessage(nextMessage);
    };

    _updateTodo = (event) => {
        const { id, updateTask, confirmEditingTask, resetEditingTask, editingTask } = this.props;

        const newMessage = editingTask.get('newMessage');

        if (!newMessage) {
            return null;
        }

        switch (event.key) {
            case 'Enter': {
                updateTask(this._getTaskShape({ id, message: newMessage }));
                confirmEditingTask(id, newMessage);
                resetEditingTask();

                break;
            }

            case 'Escape': {
                resetEditingTask();
                break;
            }

            default:
                break;
        }
    };

    _completeTask = () => {
        const { id, updateTask } = this.props;

        updateTask(this._getTaskShape({ id, completed: !this.props.completed }));
    };

    _changePriority = () => {
        const { id, updateTask } = this.props;

        updateTask(this._getTaskShape({ id, favorite: !this.props.favorite }));
    };

    _removeTask = () => {
        const { id, removeTask } = this.props;

        removeTask(id);
    };

    render () {
        const { message, completed, favorite, editingTask, id } = this.props;

        const styles = cx(Styles.task, {
            [Styles.completed]: completed,
        });

        let currentMessage = message;

        const isEditing = editingTask.get('id') === id;

        if (isEditing) {
            currentMessage = editingTask.get('newMessage');
        }

        return (
            <li className = { styles }>
                <div className = { Styles.content }>
                    <Checkbox
                        inlineBlock
                        checked = { completed }
                        className = { Styles.toggleTaskCompletedState }
                        color1 = '#3B8EF3'
                        color2 = '#FFF'
                        onClick = { this._completeTask }
                    />
                    <input
                        disabled = { !isEditing }
                        ref = { this.input }
                        type = 'text'
                        value = { currentMessage }
                        onChange = { this._editNewMessage }
                        onKeyDown = { this._updateTodo }
                    />
                </div>
                <div className = { Styles.actions }>
                    <Star
                        inlineBlock
                        checked = { favorite }
                        className = { Styles.toggleTaskFavoriteState }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._changePriority }
                    />
                    <Edit
                        inlineBlock
                        checked = { isEditing }
                        className = { Styles.updateTaskMessageOnClick }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._toggleEditingState }
                    />
                    <Remove
                        inlineBlock
                        className = { Styles.removeTask }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._removeTask }
                    />
                </div>
            </li>
        );
    }
}
