// Core
import { call, put } from 'redux-saga/effects';

// Instruments
import { url, token } from 'config/api';
import { tasksActions } from 'bus/tasks/actions';
import { uiActions } from 'bus/ui/actions';

export function* callUpdateTaskWorker ({ payload: task }) {
    try {
        yield put(uiActions.setTasksFetchingState(true));

        const response = yield call(fetch, url, {
            method:  'PUT',
            headers: {
                Authorization:  token,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify([task]),
        });

        const {
            data: [updateTask],
            message,
        } = yield call([response, response.json]);

        if (response.status !== 200) {
            throw new Error(message);
        }

        yield put(tasksActions.updateTaskSuccess(updateTask));
    } catch (error) {
        yield put(tasksActions.updateTaskFail(error));
    } finally {
        yield put(uiActions.setTasksFetchingState(false));
    }
}
